<template>
  <el-container class="common-layout" ref="divDom">
    <el-aside>
      <el-scrollbar>
        <div class="website-left" v-if="arrey.length">
          <div class="website-left-box" v-for="(item, index) in arrey" :key="index">
            <div :class="{ active: item.key == current }" @click="liClick(item.key)">
              {{ item.title }}
            </div>
          </div>
        </div>
      </el-scrollbar>
    </el-aside>
    <el-main>
      <el-scrollbar ref="scrollbarRef">
        <div class="website-right-box">
          <div class="website-right-title" v-for="(item, index) in arrey" :key="index" :id="item.key">
            <template v-if="index == 0">
              <div class="findViewSearch">
                <div class="findViewSearchHead">
                  <h1>兜兜的导航</h1>
                </div>
                <div class="findViewSearch-main">
                  <el-input v-model="searchValue" placeholder="我来帮你百度一下" @keyup.enter.native="search"></el-input>
                  <el-button @click="search">搜索</el-button>
                </div>
              </div>
              <div class="website-right-title-box">
                <h2>{{ item.title }}</h2>
              </div>
              <div class="box">
                <div class="webBox" v-for="(res, index) in item.childers" :key="index">
                  <template v-if="res.jianjie">
                    <div class="jianjie">{{ res.jianjie }} </div>
                  </template>
                  <a :href="res.url" target="_blank">
                    <div class="webUrl">
                      <img :src="res.imgUrl ? res.imgUrl : 'https://img.bidianer.com/image/icon/empty.svg'">
                      <div>{{ res.webName }}</div>
                    </div>
                  </a>
                </div>
              </div>
            </template>
            <template v-else>
              <div class="website-right-title-box">
                <h2>{{ item.title }}</h2>
              </div>
              <div class="box">
                <div class="webBox" v-for="(res, index) in item.childers" :key="index">
                  <template v-if="res.jianjie">
                    <div class="jianjie">{{ res.jianjie }} </div>
                  </template>
                  <a :href="res.url" target="_blank">
                    <div class="webUrl">
                      <img :src="res.imgUrl ? res.imgUrl : 'https://img.bidianer.com/image/icon/empty.svg'">
                      <div>{{ res.webName }}</div>
                    </div>
                  </a>
                </div>
              </div>
            </template>
          </div>
        </div>
      </el-scrollbar>
    </el-main>
  </el-container>
</template>
<script setup>
import webData from '../untils/websiteData.js';
import { ref, onMounted, computed, reactive, nextTick, watchEffect, toRef, toRefs, watch, onUnmounted } from 'vue';
import { ElMessageBox, ElMessage, ElNotification } from 'element-plus'

//#region 初始化数据

const arrey = webData.webArry
const recommendData = randomAcquisition(arrey)
function randomAcquisition(data) {
  let allData = data.reduce((e, item) => {
    return e = [...e, ...item.childers]
  }, [])
  const top20Items = allData.sort((a, b) => b.metaDataId - a.metaDataId).slice(0, 10)
  let newSort = {
    title: '近期更新',
    key: 'recommend',
    childers: top20Items
  }
  return newSort
}
arrey.unshift(recommendData)
//#endregion

//#region 搜索 
const searchValue = ref('')
function search(e) {
  const searchUrl = 'https://www.baidu.com/s?wd=' + searchValue.value
  window.open(searchUrl, '_blank');
}
//#endregion

//#region  页面滚动效果
// 滚动条
const scrollbarRef = ref(null);
// 默认选择一个 空则默认第一个
const current = ref(null);
const flag = ref(false);
const liClick = (key) => {
  flag.value = true;
  current.value = key;
  /* 
  获取到key 然后渠道对应的元素 进行滚动
   */
  // 获取父级容器
  // let content = document.querySelector(".website-right-box");
  let target = document.querySelector(`#${key}`);
  // document.getElementById(key).scrollIntoView({ behavior: 'smooth', inline: 'start' });
  scrollbarRef.value.scrollTo({
    top: target.offsetTop - 40,
    behavior: "smooth",
  })

  // content.scrollTo({
  //   // 距离顶部导航栏的距离
  //   top: target.offsetTop - 60,
  //   behavior: "smooth",
  // });
};
// 所有dom元素的id
const showKeys = reactive([]);
// -----交叉观察器
let io = new IntersectionObserver((entrys) => {
  let firstInsetId = entrys[0].target.id;
  let isInter = entrys[0].isIntersecting;
  if (isInter) {
    let isFind = showKeys.findIndex((item) => item == firstInsetId);
    if (isFind == -1) {
      showKeys.push(firstInsetId);
    }
  } else {
    let index = showKeys.findIndex((item) => item == firstInsetId);
    if (index !== -1) {
      showKeys.splice(index, 1);
    }
  }
  if (flag.value) return; //----放开是另一种效果
  let result = "";
  for (const item of arrey) {
    if (showKeys.findIndex((key) => key === item.key) !== -1) {
      result = item.key;
      break;
    }
  }
  current.value = result;
}, {
  threshold: [0, 0.25, 0.5, 1]
}
);
const startObser = () => {
  for (const item of arrey) {
    let el = document.querySelector(`#${item.key}`);
    showKeys.push(item.key);
    io.observe(el);
  }
};
onMounted(() => {
  let content = document.querySelector(".website-right-box");
  content.addEventListener("mousewheel", () => {
    flag.value = false;
  });
  setTimeout(() => {
    // 如果当前网址是 #/homeson/website
    startObser();
  }, 500)
});

onUnmounted(() => {
  io.disconnect()
})
//#endregion

ElNotification({
  title: '提示',
  type: 'info',
  message: `该页面正在开发中,目前展示内容仅供测试使用`,
  dangerouslyUseHTMLString: true,
  duration: 0,
})

</script>

<style lang="less" scoped>
.common-layout {
  height: 100vh;
  margin: 0 auto;
  max-width: 1400px;
  background-color: #f5f5f5;

  .el-aside {
    margin-top: 40px;
    margin-bottom: 40px;
    border-radius: 10px;
    overflow: hidden;
    transition: all .5s;

    .website-left {
      padding: 10px;
      padding-left: 20px;
      background-color: white;
      border-radius: 10px;
      overflow: hidden;

      .website-left-box {
        position: relative;

        p {
          cursor: pointer;
          height: 40px;
          border-radius: 5px;
          transition: all .5s;
          font-size: 14px;
          border: 1.5px dashed rgba(236, 245, 255, );
          display: flex;
          align-items: center;
          justify-content: center;
        }

        p:hover {
          background-color: #ecf5ff;
          color: #409eff;

        }

        >div {
          transition: all .5s;
          font-size: 14px;
          line-height: 40px;
          cursor: pointer;
          border-radius: 5px;
          margin: 5px 0;
          overflow: hidden; /*（文字长度超出限定宽度，则隐藏超出的内容）*/
          white-space: nowrap; /*（设置文字在一行显示，不能换行）*/
          text-overflow: ellipsis; /*（规定当文本溢出时，显示省略符号来代表被修剪的文本）*/
        }

        >div:hover {
          color: #409eff;
          background-color: #ecf5ff;
          padding-left: 20px;
        }

        div:first-child.active {
          color: #409eff;
          background-color: #ecf5ff;
          padding-left: 20px;
        }
      }

    }


  }

  .el-main {
    margin: 0 auto;
    padding: 0;
    max-width: 1400px;
    height: 100%;
    transition: all .5s;



    .website-right-box {
      padding: 0 10px 0 20px;
      height: 100%;

      .findViewSearch {
        height: 100px;

        .findViewSearchHead {
          text-align: center;
        }

        .findViewSearch-main {
          display: flex;
          max-width: 500px;
          margin: 0 auto;

        }
      }

      .website-right-title {
        padding-top: 15px;

        .website-right-title-box {
          display: inline-block;
          border-radius: 10px;
          transition: all .3s;
          cursor: pointer;

          h2 {
            user-select: none;
            color: #606266;
            font-weight: 400;
            font-size: 20px;
            transition: all .3s;
            display: flex;
            align-items: center;
            position: relative;
            margin-bottom: 5px;
          }

          h2::after {
            content: '';
            height: 2px;
            width: 0px;
            background-color: #0072ea;
            position: absolute;
            bottom: -2px;
            transition: all .5s;
          }

        }

        .website-right-title-box:hover {

          h2 {
            color: #0072ea;
          }

          h2::after {
            width: calc(100% + 10px);

          }
        }

        .el-divider {
          margin-top: 10px;
          margin-bottom: 0;
        }

        .box {
          display: flex;
          padding: 5px 0;
          flex-wrap: wrap;
          position: relative;

          .webBox {
            cursor: pointer;
            position: relative;
            display: flex;
            justify-content: space-around;
            background-color: #ffffff;
            transition: all 0.5s;
            margin: 5px 5px;
            border-radius: 5px;

            a {
              width: 100%;
              padding: 10px;
            }

            p {
              width: 100%;
              display: flex;
              align-items: center;
              justify-content: center;
              transition: all .5s;
              font-size: 22px;
              height: 42px;
            }

            .jianjie {
              position: absolute;
              display: flex;
              z-index: 9999;
              opacity: 0;
              font-size: 16px;
              border: 1px solid;

              transition: transform 0.5s, opacity 0.5s;
              background-color: #515051;
              color: white;
              padding: 8px 10px;
              border-radius: 8px;
              text-align: center;
              bottom: 50px;

              left: -5000px;
            }

            .jianjie::after {
              content: "";
              position: absolute;
              bottom: -16px;
              right: 50%;
              transform: translateX(50%);
              border: 8px solid #515051;
              border-bottom-color: transparent;
              border-left-color: transparent;
              border-right-color: transparent;
            }

            .webUrl {
              display: flex;
              align-items: flex-end;
              width: 98%;

              position: relative;

              div {
                width: 100%;
                overflow: hidden; 
                white-space: nowrap; 
                text-overflow: ellipsis;
                height: 100%;
                line-height: 22px;
                font-size: 15px;
              }

              img {
                width: 20px;
                height: 20px;
                margin-right: 5px;
              }
            }
          }

          .webBox:hover {
            box-shadow: 0 15px 30px rgba(0, 0, 0, .1);
            transform: translate3d(0, -2px, 0);

            p {
              color: #0072ea;
            }

            .jianjie {
              display: block;
              opacity: 1;
              transform: translateY(-10px);
              left: initial;
            }
          }
        }

      }
    }
  }
}

/*当页面最大宽小于1920 */
@media screen and (max-width: 1920px) {
  .webBox {
    width: 268px;
  }

  .el-aside {
    width: 200px;
  }
}

/*当页面最大宽小于400*/
@media only screen and (max-width: 450px) {
  .webBox {
    width: 200px;
  }

  .el-aside {
    width: 140px;
  }
}
</style>